import React from "react";
import logoImg from "../vod-logo.png";
import bars from "../whitebars.png";
const Logo = () => (
  <div>
    <img src={logoImg} alt="" className="logo-img" />
    <span className="logo-text">Challenger Engine</span>
    <img src={bars} alt="" className="bars-img" />
  </div>
);
export default Logo;
